@import 'Index';


.add-form {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    position: absolute;
    left: 50vw;
    top: 50vh;
    transform: translate(-50%, -50%);
    width: 500px;
    height: 800px;
    margin: 0 auto;
    border-radius: 20px;
    box-shadow: 0px 10px 20px 5px rgba(17, 17, 17, 0.281);

    .label-container {
        display: flex;
        flex-direction: row;
        // justify-content: left;
        align-items: center;
        padding: 30px 70px 0 20px;
        text-align: right;
    }

    input {
        width: 300px;
        height: 30px;
        margin-left: 10px;
        padding: 10px;


        border: none;
        border-radius: 10px;

        font-family: $sofia-font;
        font-weight: 500;
        font-size: 18px;

        transition: .2s;
    }

    label.form-part {}


    label {
        flex-grow: 1;
        display: inline-block;
        font-family: $sofia-font;
        font-size: 18px;
    }

    .radio-label {
        margin-top: 30px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin: 30px 150px 0 150px;

        .radio-option {
            display: flex;
            align-items: center;
            // margin-right: 20px;
        }

        input {
            width: 15px;

            // border: 2px solid red
            margin-right: 10px;
        }
    }

    textarea {
        padding: 10px;
        width: 300px;
        margin-left: 10px;
        height: 100px;
        border: none;
        border-radius: 10px;

        font-family: $sofia-font;
        font-weight: 500;
        font-size: 18px;
        transition: .2s;
    }



    .submitButton {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 80px 0;
        position: relative;

        div {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            height: 100px;
            width: 100px;
            border-radius: 100%;


            button {
                position: absolute;
                top: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                width: 70px;
                height: 70px;
                transition: .1s;
                color: $white;




                text-align: center;
                line-height: 40px;
                border-radius: 100%;

                font-size: 25px;
                font-weight: bold;
                font-family: $sofia-font;
                border: none;
            }

            button:hover {
                cursor: pointer;
                color: $pink;
            }

            button:active {
                font-size: 20px;
            }

            .spinLoad {
                // animation: loadSpin 1s;
            }
        }

    }
}

.slideIn {
    animation: swipeIn .5s ease;
}

.slideOut {
    animation: swipeOut .5s ease forwards;

}

@keyframes swipeIn {
    0% {
        transform: translate(-50%, 100%);
    }

    100% {
        transform: translate(-50%, -50%);
    }

}

@keyframes swipeOut {

    0% {
        transform: translate(-50%, -50%);
    }

    100% {
        transform: translate(-50%, 100%);
    }
}