$white: #D9D9D9;
$whited: #b8b8b8;
$whiter: rgb(241, 241, 241);
$light-grey: #2A2A2A;
$lighter-grey: #3b3b3b;
$grey: #1F1F1F;
$dark-grey: #1B1B1B;
$pindsdk: #a32c42;
$pink: #CD3752;
$grey-text: rgb(143, 143, 143);

$jura-font: 'Jura', sans-serif;
$sofia-font: 'Sofia Sans', sans-serif;

*::-webkit-scrollbar {
  display: none;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@mixin dark-theme {
  .background {
    background-color: $dark-grey;
  }



  a {
    color: $white;
  }

  .header {
    background-color: $dark-grey;

    .header__menu {
      color: $white;
    }

    .header__mode {
      color: $white;
    }
  }

  // Add Patient 

  .add-form {
    background-color: $light-grey;
    color: $white;

    box-shadow: 0px 10px 20px 5px rgba(17, 17, 17, 0.281);

    input {
      background-color: $grey;
      color: $grey-text;
    }

    input:focus {
      background-color: $lighter-grey;
      outline: $light-grey;
    }

    label {
      color: $white;
    }


    textarea {
      background-color: $grey;

      color: $white;
    }

    textarea:focus {
      background-color: $lighter-grey;
      outline: $light-grey;
    }

    .submitButton {

      div {
        background: #2E2E2E;
        box-shadow: inset -19px 21px 50px -15px rgba(0, 0, 0, 0.41);

        button {
          color: $white;
          background: #2E2E2E;
          box-shadow: -8px 11px 14px 1px rgba(0, 0, 0, 0.44), inset 31px -31px 45px -33px rgba(0, 0, 0, 0.67);
        }

        button:active {
          background: #2E2E2E;
        }

      }

    }
  }


  .home {
    color: $white;
  }

  .wave {
    background-image: url("data:image/svg+xml,%3Csvg width='1920' height='376' viewBox='0 0 1920 376' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1018.52 74.8049C631.771 -74.8083 178.361 34.1206 0 107.287V376H1920V107.287C1780.66 158.798 1405.28 224.418 1018.52 74.8049Z' fill='%231F1F1F'/%3E%3C/svg%3E");
  }

  .wave2 {
    background-image: url("data:image/svg+xml,%3Csvg width='1920' height='376' viewBox='0 0 1920 376' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1018.52 74.8049C631.771 -74.8083 178.361 34.1206 0 107.287V376H1920V107.287C1780.66 158.798 1405.28 224.418 1018.52 74.8049Z' fill='%232A2A2A' /%3E%3C/svg%3E");
  }


  .wave3 {
    background-image: url("data:image/svg+xml,%3Csvg width='1920' height='376' viewBox='0 0 1920 376' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1018.52 74.8049C631.771 -74.8083 178.361 34.1206 0 107.287V376H1920V107.287C1780.66 158.798 1405.28 224.418 1018.52 74.8049Z' fill='%23a32c42' /%3E%3C/svg%3E");
  }


  .wave4 {
    background-image: url("data:image/svg+xml,%3Csvg width='1920' height='376' viewBox='0 0 1920 376' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1018.52 74.8049C631.771 -74.8083 178.361 34.1206 0 107.287V376H1920V107.287C1780.66 158.798 1405.28 224.418 1018.52 74.8049Z' fill='%23CD3752' /%3E%3C/svg%3E");
  }

  .base-wrapper {
    background-color: $grey;
    height: 100vh;

    input {
      background-color: $light-grey;
      color: $grey-text;
    }

    input:focus {
      background-color: $lighter-grey;
    }

    input:hover {
      background-color: $lighter-grey;
    }
  }

  .patient-box {
    background-color: $light-grey;
    color: $white;
    box-shadow: 0px 10px 20px 5px rgba(17, 17, 17, 0.281);

    .none {
      outline: 4px solid rgb(255, 255, 255)
    }
  }

  .patient-box:hover {
    background-color: $lighter-grey;
  }

  .profile-pop-bc {
    background-color: rgba(31, 31, 31, 0.815);

    i {
      color: $white;
    }

    .profile-pop__data {
      background-color: $light-grey;
      color: $white;
    }
  }

  .background {
    .callendar-day-names {
      .callendar-day-name {
        color: $white;
      }
    }



    .callendar-wrap {


      .grid-item {
        background-color: $grey;
        color: $white;

      }

      .grid-item:hover {
        background-color: $light-grey;
      }


      .grid-item-day {
        color: $white;
      }

    }

    .callendar-arrows {
      color: $white;

    }
  }
}

@mixin light-theme {

  .background {
    background-color: $whiter;
  }

  a {
    color: $dark-grey;
  }

  .header {
    background-color: $whiter;
    box-shadow: 0px 10px 10px 5px rgba(17, 17, 17, 0.041);

    .header__menu {
      color: $dark-grey;
    }

    .header__mode {
      color: $dark-grey;
    }
  }

  // Add Patient 

  .add-form {
    background-color: $whiter;

    box-shadow: 0px 10px 20px 5px rgba(17, 17, 17, 0.068);

    input {
      background-color: $white;
      color: $dark-grey
    }

    input:focus {
      background-color: $whited;
      outline: $light-grey;
    }

    label {
      color: $dark-grey;
    }


    textarea {
      background-color: $white;

      color: $dark-grey;
    }

    textarea:focus {
      background-color: $whited;
      outline: $dark-grey;
    }

    .submitButton {

      div {
        background: $white;
        box-shadow: inset -19px 21px 10px -10px rgba(128, 128, 128, 0.712);

        button {
          color: $dark-grey;
          background: $white;
          box-shadow: -8px 11px 5px 1px rgba(148, 148, 148, 0.44),
            inset 31px -31px 20px -20px rgba(110, 110, 110, 0.67);
        }

        button:active {
          background: #c0c0c0;
          box-shadow: inset -31px 31px 20px -20px rgba(83, 83, 83, 0.67);
        }

      }

    }


  }


  .home {
    color: $dark-grey;
  }

  .wave {
    background-image: url("data:image/svg+xml,%3Csvg width='1920' height='376' viewBox='0 0 1920 376' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1018.52 74.8049C631.771 -74.8083 178.361 34.1206 0 107.287V376H1920V107.287C1780.66 158.798 1405.28 224.418 1018.52 74.8049Z' fill='%231F1F1F'/%3E%3C/svg%3E");
  }

  .wave2 {
    background-image: url("data:image/svg+xml,%3Csvg width='1920' height='376' viewBox='0 0 1920 376' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1018.52 74.8049C631.771 -74.8083 178.361 34.1206 0 107.287V376H1920V107.287C1780.66 158.798 1405.28 224.418 1018.52 74.8049Z' fill='%232A2A2A' /%3E%3C/svg%3E");
  }


  .wave3 {
    background-image: url("data:image/svg+xml,%3Csvg width='1920' height='376' viewBox='0 0 1920 376' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1018.52 74.8049C631.771 -74.8083 178.361 34.1206 0 107.287V376H1920V107.287C1780.66 158.798 1405.28 224.418 1018.52 74.8049Z' fill='%23a32c42' /%3E%3C/svg%3E");
  }


  .wave4 {
    background-image: url("data:image/svg+xml,%3Csvg width='1920' height='376' viewBox='0 0 1920 376' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1018.52 74.8049C631.771 -74.8083 178.361 34.1206 0 107.287V376H1920V107.287C1780.66 158.798 1405.28 224.418 1018.52 74.8049Z' fill='%23CD3752' /%3E%3C/svg%3E");
  }


  .base-wrapper {
    background-color: $whiter;
    height: 100vh;

    input {
      background-color: $whited;
      color: $dark-grey;
    }

    input:focus {
      background-color: $white;
    }

    input:hover {
      background-color: $white;
    }
  }

  .patient-box {
    background-color: $whiter;
    color: $dark-grey;
    box-shadow: 0px 10px 10px 5px rgba(17, 17, 17, 0.082);

    .none {
      outline: 4px solid rgb(26, 26, 26)
    }
  }

  .patient-box:hover {
    background-color: $whited;
  }

  .profile-pop-bc {
    background-color: rgba(214, 214, 214, 0.815);

    i {
      color: $dark-grey;
    }

    .profile-pop__data {
      background-color: $whiter;
      color: $dark-grey;
    }
  }

  .background {
    .callendar-day-name {
      color: $dark-grey;
    }


    .callendar-wrap {


      .grid-item {
        background-color: $white;
        color: $dark-grey;

      }

      .grid-item:hover {
        background-color: $whited;
      }


      .grid-item-day {
        color: $dark-grey
      }

    }

    .callendar-arrows {
      color: $dark-grey;

    }
  }
}

.dark-mode {
  @include dark-theme;
}

.light-mode {
  @include light-theme;
}